.titleMoviePage {
  display: flex;
  font-weight: 400;
  font-size: 37px;
  color: #7d7373;
  justify-content: flex-start;
  margin-left: 45px;
  margin-top: 75px;
}

.movieContainer {
  display: flex;
  height: 755px;
  justify-content: center;
  align-items: stretch;
  text-align: center;
  align-self: stretch;
}

.movieCard {
  margin-top: 50px;
  /*margin-bottom: 150px; */
  display: flex;
  height: 600px;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  padding: 15px;
  border-radius: 50px;
  transition: all 0.5s;
  box-shadow: 4px 4px 15px 2px rgba(#0000, 0.75);
  z-index: 0;
  color: white;
}

.cardTitle {
  font-size: 22px;
  /* margin-top: -15px; */
  height: 75px;
}
.cardImg {
  vertical-align: middle;
  border-style: none;
  margin-top: 5px;
  margin-bottom: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.cardImg img {
  width: 175px;
  height: 225px;
}

.description {
  text-align: justify;
  height: 185px;
  overflow-y: scroll;
  font-size: 13px;
}

.description::-webkit-scrollbar {
  display: none;
}

.movieCard:not(:first-child) {
  margin-left: -8rem;
}

.movieCard:hover {
  transform: translateY(-1rem);
  z-index: 1;
}

.movieCard:hover ~ .movieCard {
  transform: translateX(8rem);
}

.fade-enter {
  opacity: 0;
  transform: scale(0.9);
}
.fade-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 500ms, transform 500ms;
  -webkit-transition: opacity 500ms, transform 500ms;
  -moz-transition: opacity 500ms, transform 500ms;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 500ms, transform 500ms;
  -webkit-transition: opacity 500ms, transform 500ms;
  -moz-transition: opacity 500ms, transform 500ms;
}
